import axios from 'axios';

export const baseUrl = 'http://49.233.185.168:3000';

// axios
const axiosInstance = axios.create({
  baseURL: baseUrl
});

axiosInstance.interceptors.response.use(
  res => res.data,
  err => {
    console.log(err, "");
  }
);

export {
  axiosInstance
};

